<template>
  <div class="video-slide">
    <div
      id="player"
      data-plyr-provider="vimeo"
      data-plyr-embed-id="757894953"
    ></div>
  </div>
</template>
<script>
import Plyr from "plyr";
import "plyr/dist/plyr.css";

export default {
  mounted() {
    const player = new Plyr("#player", {
      controls: ["play-large"],
      fullscreen: { enabled: false },
    });
    window.player = player;

    let audioBox = document.getElementById("audio");

    player.on("playing", () => {
      audioBox.pause();


    });
    audioBox.addEventListener("playing", () => {
      player.pause();
    });
      const videoWrapper = document.getElementsByClassName("plyr__video-wrapper")[0];
  videoWrapper.addEventListener("click", event => {
    player.togglePlay();
    event.stopPropagation(); // Necessary or the video will toggle twice => no playback
  });

  player.toggleControls(false);
  },
};
</script>
